<template>
  <div>
    <v-navigation-drawer
      temporary
      :right="right"
      v-model="rightDrawer"
      fixed
      app
      color="primary"
    >
      <v-list>
        <router-link tag="v-list-tile" :to="{ name: 'home' }">
          <v-list-tile-action>
            <v-icon>dashboard</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>Home</v-list-tile-title>
        </router-link>
        <v-divider :inset="true"></v-divider>
        <router-link tag="v-list-tile" :to="{ name: 'about_us' }">
          <v-list-tile-action>
            <v-icon>question_answer</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>About Us</v-list-tile-title>
        </router-link>
        <v-divider :inset="true"></v-divider>
        <router-link tag="v-list-tile" :to="{ name: 'testimonials' }">
          <v-list-tile-action>
            <v-icon>message</v-icon>
          </v-list-tile-action>
          <v-list-tile-title>Testimonials</v-list-tile-title>
        </router-link>
        <v-divider :inset="true" color="secondary"></v-divider>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      app
      :clipped-left="clipped"
      dark
      color="secondary"
      flat>
      <v-toolbar-title v-text="title" class="display-2"></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click.stop="rightDrawer = !rightDrawer">
        <v-icon>menu</v-icon>
      </v-btn>
    </v-toolbar>
  </div>
</template>
<script>
import { VDivider } from 'vuetify'
export default {
  data () {
    return {
      clipped: false,
      drawer: true,
      fixed: false,
      items: [{
        icon: 'bubble_chart',
        title: 'Inspire'
      }],
      miniVariant: false,
      right: true,
      rightDrawer: false,
      title: 'Stock Tech'
    }
  },
  components: {
    VDivider
  },
  mounted () {
    // this.$vuetify.application.top = 0
    // this.$vuetify.application.footer = 0
  }
}
</script>



